body {
    color: #2E4172;
}

.app-header {
    display: flex;
    padding: 1% 2%;
    background: #2E4172;
    color: #ffffff;
    font-size: 1.5rem;
}

.app-content {
    margin-left: 1%;
    margin-right: 1%;
    margin-top: 1%;
}

.app-global-summary {
    display: flex;
    justify-content: space-around;
    margin-bottom: 3%;
}

.app-global-summary-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1%;
    border: 1px solid #2E4172;
    box-shadow: 5px 10px #2e4172c9;
    .count {
        font-size: 2rem;
    }
}

.app-country-summary-heading {
    font-weight: 400;
    display: flex;
    justify-content: space-between;
    width: 39%;
}

.app-country-summary-content {
    display: flex;
}

.app-country-summary-table {
    width: 40%;
    margin-right: 5%;
}

.app-country-summary-table-heading {
    display: flex;
    border: 0.25px solid #2E4172;
    padding: 1%;
    font-size: 1.2rem;
    font-weight: 600;
    background: #2e417259;
    width: calc(100% - 34px);
    div {
        width: 25%;
    }
}

.app-country-summary-table-data {
    height: 60vh;
    overflow-y: scroll;
}

.app-country-summary-country-item {
    padding: 1%;
    display: flex;
    justify-content: space-between;
    border: 0.25px solid #2E4172;
    transition: all 0.25s;
    span {
        width: 25%;
        background: transparent;
    }
    &:hover {
        cursor: pointer;
        background: #2e417285;
        color: #ffffff;
    }
}

.app-country-summary-details {
    width: 40%;
}

.app-country-summary-details-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1%;
    border: 1px solid #2E4172;
    margin: 1%;
    box-shadow: 2px 4px #2e4172c9;
    border-radius: 20px;
    .heading {
        font-size: 1.5rem;
        font-weight: 600;
    }
}

.app-country-summary-details-category {
    display: flex;
    justify-content: space-around;
    margin-top: 3%;
    padding-bottom: 1%;
    border-bottom: 1px solid #2E4172;
    >div {
        width: 20%;
    }
}